            },
            disotherImage() {
                this.show_mes = false;
				var id=this.infoMessage.id;
				uni.setStorageSync(app.noticeTime,this.nowDate);
                //跳转至详情页
                uni.navigateTo({
                    url: '/pages/index/mediaMessage?id='+ id
                }); 
            }
